@charset "utf-8";
/* CSS Document */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  src: local('Roboto Light'), local('Roboto-Light'), url(https://fonts.gstatic.com/s/roboto/v18/0eC6fl06luXEYWpBSJvXCBJtnKITppOI_IvcXXDNrsc.woff2) format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 300;
  src: local('Montserrat Light'), local('Montserrat-Light'), url(https://fonts.gstatic.com/s/montserrat/v12/IVeH6A3MiFyaSEiudUMXExNcqx07xvyppV96iFRdwiM.woff2) format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
html, body {
    height: 100%;
	width:100%;
    font-size: 16px;
    color: #f5f5f5;
    font-family: "Nunito", sans-serif;
    vertical-align: baseline;
	padding:0;
	margin:0;
    overflow: auto;
	-webkit-text-size-adjust: auto;
}
::-webkit-scrollbar {width: 5px;}
::-webkit-scrollbar-track {
    background-color: #ebebeb;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #8ba122; 
}
ul,label{
	margin:0;
	padding:0;
}
body a {
	text-decoration:none;
	transition:0.5s all;
	-webkit-transition:0.5s all;
	-moz-transition:0.5s all;
	-o-transition:0.5s all;
	-ms-transition:0.5s all;
}
body .btn a {color: #fff !important;}
a:active,a:hover,a:focus {outline: none; text-decoration: none;}
.container-fluid{
	width:100%;
	padding-left:0px;
	padding-right:0px;
	margin-left:0px;
	margin-right:0px;
}
button:focus {outline: none;}
:before,
:after {
    transition: all .3s;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
}

/***********============================================== header_topbar =====================================================***********/
@media only screen and (max-width: 600px) {.header_topbar{padding:5px 20px;}}
@media only screen and (min-width: 600px) {.header_topbar{padding:5px 20px;}}
@media only screen and (min-width: 768px) {.header_topbar{padding:0px 20px;}} 
@media only screen and (min-width: 992px) {.header_topbar{padding:0px 50px 0px 50px;}} 
@media only screen and (min-width: 1200px) {.header_topbar{padding:0px 80px 0px 80px;}}
@media only screen and (min-width: 1500px) {.header_topbar{padding:0px 140px 0px 140px;}}
.header_topbar .mt10{margin-top:13px;}
.header_topbar .mt15{margin-top:15px;}
.header_topbar .mt30{margin-top:30px;}
.header_topbar{
	position: relative;
	color: #797979;
    font-weight: 400;
	display:block;
	z-index:1;
    font-size: 15px;
    background: #f8f9fa !important;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-bottom-color: rgba(255,255,255,.2);
}
.header_topbar ul.pull-left>li{list-style:none;}
.header_topbar ul.pull-left>li .inner{position:relative; font-family: 'Quicksand', sans-serif; padding:13px 0px; color:#fff;}
.header_topbar ul.pull-left>li .inner:before {
    content: "";
    position: absolute;
    right: -23px;
    top: 0;
    bottom: -1px;
    transform: skew(-37deg);
    z-index: -1;
    width: 1200px;
    border-right: 0;
    background: #8ba122;
}
.header_topbar .links {margin-top:17px;}
.header_topbar .links li{
	font-family: 'Quicksand', sans-serif;
	font-weight: 400;
	position: relative;
    display: inline-block;
    line-height: 22px;
    padding-right: 11px;
    margin-right: 10px;
}
.header_topbar .links li a{color:#0d1e50; font-weight:500;}
.header_topbar .links li:before{
	position: absolute;
    top: 4px;
    right: 0;
    bottom: 4px;
    width: 1px;
    background: #fff;
    content: "";
}
.header_topbar .links li .fa{color: #8ba122; font-size:14px; font-weight:400; padding-right:10px;}
.header_topbar .social:last-child{margin-bottom: 0;}
.header_topbar .social li{
	display: inline-block;
    float: left; 
    border: 1px solid #efefef;
    border-bottom: none;
    border-right: none;
    border-top: none;
    width: 40px;
	line-height:50px;
}
.header_topbar .social li a{color:#0d1e50;}
.header_topbar .social li a i{
    font-size: 13px;
	padding-top:5px;
    line-height: 30px;
} 
/***********============================================== header_topbar =====================================================***********/

/***********============================================== header_bottom =====================================================***********/
@media only screen and (max-width: 600px) {.header_bottom{padding: 0px 0px 0px 0px;} .logo{text-align:center;} .logo img{margin:0 auto; text-align:center;}}
@media only screen and (min-width: 600px) {.header_bottom{padding: 0px 0px 0px 0px;} .logo{text-align:center;} .logo img{margin:0 auto; text-align:center;}}
@media only screen and (min-width: 768px) {.header_bottom{padding: 10px 20px 10px 20px;} .logo{text-align:left;} .logo img{margin:0px 0px; text-align:left;}} 
@media only screen and (min-width: 992px) {.header_bottom{padding: 10px 50px 10px 50px;} .logo{text-align:left;} .logo img{margin:0px 0px; text-align:left;}} 
@media only screen and (min-width: 1200px) {.header_bottom{padding: 10px 80px 10px 80px;} .logo{text-align:left;} .logo img{margin:0px 0px; text-align:left;}}
@media only screen and (min-width: 1500px) {.header_bottom{padding: 10px 140px 10px 140px;} .logo{text-align:left;} .logo img{margin:0px 0px; text-align:left;}}
.header_bottom{display:block; position:relative; z-index:9; background:#fff; border-bottom: 2px solid #ddd;}
.header_bottom .mt30{margin-top:30px;}
.header_bottom .mt15{margin-top:15px;}
.header_bottom{position:relative; box-shadow: 0 0 60px 0 rgba(0, 0, 0, .07);  -webkit-box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1); z-index:25;}
header{position:relative; width:100%; font-size:16px; font-weight:400; font-family: 'Quicksand', sans-serif;}
nav{position:relative;}
#cssmenu,#cssmenu ul,#cssmenu ul li,#cssmenu ul li a,#cssmenu #head-mobile{border:0;list-style:none;line-height:1;display:block;position:relative;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}
#cssmenu:after,#cssmenu > ul:after{content:"";display:block;clear:both;visibility:hidden;line-height:0;height:0}
#cssmenu #head-mobile{display:none}
#cssmenu{font-family: 'Quicksand', sans-serif;}
#cssmenu .pull-right{padding-top:10px;}
#cssmenu > ul {padding-left:0px;}
#cssmenu > ul > li{float:left;z-index:1000;}
#cssmenu > ul > li > a{padding:17px 17px;font-size:16px; display:block; text-decoration:none; text-transform:capitalize; font-weight:600; color:#222; font-family: 'Quicksand', sans-serif;}
#cssmenu > ul > li:hover > a,#cssmenu ul li.active a{color:#222;}
#cssmenu > ul > li:hover,#cssmenu ul li.active:hover,#cssmenu ul li.active,#cssmenu ul li.has-sub.active:hover{-webkit-transition:background .3s ease;-ms-transition:background .3s ease;transition:background .3s ease;}
#cssmenu > ul > li.has-sub > a{padding-right:30px}
#cssmenu > ul > li.has-sub > a:after{position:absolute;top:24px;right:11px;width:8px;height:2px;display:block;background:#222;content:''}
#cssmenu > ul > li.has-sub > a:before{position:absolute;top:21px;right:14px;display:block;width:2px;height:8px;background:#222;content:'';-webkit-transition:all .25s ease;-ms-transition:all .25s ease;transition:all .25s ease}
#cssmenu > ul > li.has-sub:hover > a:before{top:23px;height:0}
#cssmenu ul ul{position:absolute;left:-9999px;background: #f7f7f7 none repeat scroll 0 0;
    box-shadow: 0 8px 11px 4px rgb(158 158 158 / 10%);
    -webkit-box-shadow: 0 8px 11px 4px rgb(158 158 158 / 10%);
    border-radius: 0px 0px 5px 5px;  padding-bottom:8px !important;}
#cssmenu ul ul li{height:0;-webkit-transition:all .25s ease;-ms-transition:all .25s ease;transition:all .25s ease;}
#cssmenu li:hover > ul{left:auto}
#cssmenu li:hover > ul > li{height:35px}
#cssmenu ul ul ul{margin-left:100%;top:0; border-radius:3px; padding-bottom:8px !important;}
#cssmenu ul ul li a{padding:15px 10px; width:200px; font-size:14px; text-decoration:none; color:#222; font-weight:500; text-align:left;
    border-bottom-width: 1px;
    border-bottom-color: #f1f1f1;
    border-bottom-style: solid;}
#cssmenu ul ul li:last-child > a,#cssmenu ul ul li.last-item > a{border-bottom:0}
#cssmenu ul ul li:hover > a,#cssmenu ul ul li a:hover{color:#222;}
#cssmenu ul ul li.has-sub > a:after{position:absolute;top:16px;right:11px;width:8px;height:2px;display:block;background:#0d1e50;content:''}
#cssmenu ul ul li.has-sub > a:before{position:absolute;top:14px;right:14px;display:block;width:2px;height:8px;background:#0d1e50;content:'';-webkit-transition:all .25s ease;-ms-transition:all .25s ease;transition:all .25s ease}
#cssmenu ul ul > li.has-sub:hover > a:before{top:17px;height:0}
#cssmenu ul ul li.has-sub:hover,#cssmenu ul li.has-sub ul li.has-sub ul li:hover{background:#bce4ed; color:#fff;}
#cssmenu ul ul ul li.active a{border-left:1px solid #0269ac;}
#cssmenu > ul > li.has-sub > ul > li.active > a,#cssmenu > ul ul > li.has-sub > ul > li.active> a{border-top:1px solid #0269ac;}

@media screen and (max-width:1000px){
.navigation header{padding:15px 0px 10px 0px !important; margin-top:0px;}
header{background:#222; font-family: 'Quicksand', sans-serif; padding:10px 0px; margin-top:20px;}
nav{width:100%; background:#222;}
#cssmenu{width:100%;}
#cssmenu ul{width:100%;display:none; background:#222; margin-top:30px; padding-left:0px;}
#cssmenu ul li{width:100%;border-top:1px solid #ccc; background:#222;}
#cssmenu > ul > li > a{padding:17px;font-size:16px; display:block; text-decoration:none; text-transform:uppercase;font-weight:600; color:#fff;font-family: 'Quicksand', sans-serif; text-align:left;}
#cssmenu > ul > li:hover > a,#cssmenu ul li.active a{color:#fff;}
#cssmenu ul li:hover{background:#ccc;}
#cssmenu ul ul li,#cssmenu li:hover > ul > li{height:auto;}
#cssmenu ul li a,#cssmenu ul ul li a{width:100%;border-bottom:0;background:#222;}
#cssmenu > ul > li{float:none}
#cssmenu ul ul li a{padding-left:25px}
#cssmenu ul ul li{background: #222;  /* fallback for old browsers */}
#cssmenu ul ul li:hover{background:#222!important; color:#fff;}
#cssmenu ul ul ul li a{padding-left:35px}
#cssmenu ul ul li a{color:#fff;background:none}
#cssmenu ul ul li:hover > a,#cssmenu ul ul li.active > a{color:#fff}
#cssmenu ul ul,#cssmenu ul ul ul{position:relative;left:0;width:100%;margin:0;text-align:left}
#cssmenu > ul > li.has-sub > a:after,#cssmenu > ul > li.has-sub > a:before,#cssmenu ul ul > li.has-sub > a:after,#cssmenu ul ul > li.has-sub > a:before{display:none}
#cssmenu #head-mobile{display:block;padding:23px;color:#ddd;font-size:15px;font-weight:700}
.button{width:55px;height:46px;position:absolute;right:0;top:0;cursor:pointer;z-index: 12399994; margin-top:0px; background:#222;}
.button:after{position:absolute;top:22px;right:20px;display:block;height:4px;width:20px;border-top:2px solid #fff;border-bottom:2px solid #fff;content:''}
.button:before{-webkit-transition:all .3s ease;-ms-transition:all .3s ease;transition:all .3s ease;position:absolute;top:16px;right:20px;display:block;height:2px;width:20px;background:#fff;content:''}
.button.menu-opened:after{-webkit-transition:all .3s ease;-ms-transition:all .3s ease;transition:all .3s ease;top:23px;border:0;height:2px;width:19px;background:#fff;-webkit-transform:rotate(45deg);-moz-transform:rotate(45deg);-ms-transform:rotate(45deg);-o-transform:rotate(45deg);transform:rotate(45deg)}
.button.menu-opened:before{top:23px;background:#fff;width:19px;-webkit-transform:rotate(-45deg);-moz-transform:rotate(-45deg);-ms-transform:rotate(-45deg);-o-transform:rotate(-45deg);transform:rotate(-45deg)}
#cssmenu .submenu-button{position:absolute;z-index:99;right:0;top:0;display:block;border-left:1px solid #444;height:46px;width:46px;cursor:pointer}
#cssmenu .submenu-button.submenu-opened{background:#222;}
#cssmenu ul ul .submenu-button{height:34px;width:34px}
#cssmenu .submenu-button:after{position:absolute;top:22px;right:19px;width:8px;height:2px;display:block;background:#fff;content:''}
#cssmenu ul ul .submenu-button:after{top:15px;right:13px}
#cssmenu .submenu-button.submenu-opened:after{background:#fff}
#cssmenu .submenu-button:before{position:absolute;top:19px;right:22px;display:block;width:2px;height:8px;background:#fff;content:''}
#cssmenu ul ul .submenu-button:before{top:12px;right:16px}
#cssmenu .submenu-button.submenu-opened:before{display:none}
#cssmenu ul ul ul li.active a{border-left:none}
#cssmenu > ul > li.has-sub > ul > li.active > a,#cssmenu > ul ul > li.has-sub > ul > li.active > a{border-top:none}
}

.header_bottom .btn{
	font-family: "Nunito", sans-serif;
	padding: 14px 25px;
    text-align: center;
    line-height: 17px;
    font-weight: 700;
    font-size: 14px;
    border-radius: 3px;
	color: #fff;
	margin-top:8px;
    background-color: #8ba122;
    text-transform: uppercase;
}
.header_bottom .btn a{color: #fff !important;}
.header_bottom .btn:hover{background:#222;}
.pl{padding-left:0px;} .pr{padding-right:0px; }
/***********============================================== header_bottom =====================================================***********/

/***********============================================== slider =====================================================***********/
@media only screen and (max-width: 600px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:15%;} 
	.slider .carousel-caption h2{font-size: 20px;  width:100%; font-weight: 500; line-height: 32px;}
}
@media only screen and (min-width: 600px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:15%;} 
	.slider .carousel-caption h2{font-size: 20px;  width:100%; font-weight: 500; line-height: 32px;}
}
@media only screen and (min-width: 768px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:18%;} 
	.slider .carousel-caption h2{font-size: 40px; width:100%; font-weight: 600; line-height: 50px;}
} 
@media only screen and (min-width: 992px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:16%;} 
	.slider .carousel-caption h2{font-size: 64px; width:100%; font-weight: 700; line-height: 80px;}
} 
@media only screen and (min-width: 1200px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:16%;} 
	.slider .carousel-caption h2{font-size: 64px; width:100%; font-weight: 700; line-height: 80px;}
}
@media only screen and (min-width: 1500px) {
	.slider{margin-top:0px;} 
	.slider .carousel-caption{top:18%;} 
	.slider .carousel-caption h2{font-size: 70px; width:100%; font-weight: 700; line-height: 80px;}
}
.slider .carousel-caption{
	position:absolute;
	left:5%;
	text-align:left;
}
.slider .carousel-caption h6{
    font-weight: 400;
    margin-bottom: 20px;
    color: #ffffff;
    font-size: 18px;
    padding-left: 40px;
}
.slider .carousel-caption h6:before{
    position: absolute;
    content: '';
    left: 20px;
    top: 7px;
    width: 30px;
    height: 7px;
    border-top: 2px solid #8ba122;
    border-bottom: 2px solid #8ba122;
}
.slider .carousel-caption h2{
	font-family: 'Quicksand', sans-serif;
    text-transform: capitalize;
    padding-bottom: 0;
    color: #fff;
	margin-bottom: 15px;
    position: relative;
    padding-bottom: 0px;
	margin-top:0px;
}
.slider .carousel-caption h2 span{font-weight: 300;}
.slider .carousel-caption p{
	font-family: "Nunito", sans-serif;
	font-size: 17px;
    font-weight: 400;
	line-height:27px;
	color:#fff;
    margin-bottom: 30px;
}
.slider .carousel-caption .btn{
	font-family: "Nunito", sans-serif;
	padding: 18px 40px;
    text-align: center;
    line-height: 17px;
    font-weight: 500;
    font-size: 15px;
	margin-top:0px;
    border-radius: 0px;
	color: #fff;
    background-color: #8ba122;
    text-transform: uppercase;
}
.slider .carousel-caption .btn:hover{background:#222;} 		
.slider .carousel.carousel-fade .carousel-item {
    display: block;
    opacity: 0;
    transition: opacity ease-out 1.7s;
    left: 0;
    top: 0;
    position: absolute;
}
.slider .carousel.carousel-fade .carousel-item.active {opacity: 1 !important;}
.slider .carousel.carousel-fade .carousel-item:first-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.slider .carousel.carousel-fade .carousel-item:second-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.slider .carousel-item .img-fluid {margin: 0 auto; width:100% !important;}
/***********============================================== slider =====================================================***********/

/***********============================================== section1 =====================================================***********/
@media only screen and (max-width: 600px) {.section1{padding:0px 20px 80px 20px; text-align:left;}}
@media only screen and (min-width: 600px) {.section1{padding:0px 20px 80px 20px; text-align:left;}}
@media only screen and (min-width: 768px) {.section1{padding:50px 20px 80px 20px; text-align:left;}} 
@media only screen and (min-width: 992px) {.section1{padding:50px 50px 80px 50px; text-align:left;}} 
@media only screen and (min-width: 1200px) {.section1{padding:50px 100px 80px 100px; text-align:left;}}
@media only screen and (min-width: 1500px) {.section1{padding:50px 160px 80px 160px; text-align:left;}} 
.section1 .mt30{margin-top:30px;}
.section1 .mt15{margin-top:15px;}
.section1 h6{
    font-family: "Nunito", sans-serif;
    position: relative;
    z-index: 1;
    color: #8ba122;
    font-size: 16px;
    font-weight: 700;
    padding-left: 45px;
    margin-bottom: 15px;
    display: inline-block;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.section1 .separator{
    position: absolute;
    left: 0px;
    top: 7px;
    width: 30px;
    height: 7px;
}
.section1 .separator:before{
    position: absolute;
    content: '';
    left: 0px;
    top: 0px;
    width: 25px;
    height: 2px;
    background-color: #111111;
}
.section1 .separator:after{
    position: absolute;
    content: '';
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 2px;
    background-color: #111111;
}
.section1 h2{
    font-family: 'Quicksand', sans-serif;
    position: relative;
    color: #222222;
    font-size: 36px;
    font-weight: 700;
    line-height: 43px;
    margin-bottom: 20px;
    text-transform: capitalize;
}
.section1 p{
    font-family: "Nunito", sans-serif;
    font-size: 16px;
    color: #555555;
    font-weight: 400;
    line-height: 26px;
}
.section1 h3{
    font-family: 'Mukta', sans-serif;
    position: relative;
    font-size: 20px;
    line-height: 32px;
    font-weight: 700;
    color: #222;
    margin-top: 5px;
}
.section1 .btn{
    font-family: "Nunito", sans-serif;
    position: relative;
    display: inline-block;
    font-size: 16px;
    line-height: 30px;
    color: #ffffff;
    font-weight: 500;
    overflow: hidden;
    background: none;
    border-radius: 0px;
    margin-top: 10px;
    padding: 16px 44px;
    text-transform: capitalize;
    background-color: #8ba122; 
}
.section1 .btn:before {
    position: absolute;
    content: '';
    top: 0;
    left: 97%;
    width: 100%;
    height: 100%;
    z-index: 1;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
    background-color: #222222;
}
.section1 .btn:hover{background: #222;}
.section1 .img{position: relative;}
.section1 .img img{
    position: relative;
    height: auto;
    max-width: 100%;
    display: block;
}
.section1 .content{
    position: absolute;
    right: 0px;
    bottom: 30px;
}
.section1 .content:before {
    position: absolute;
    content: '';
    right: 0px;
    bottom: -25px;
    width: 0;
    height: 0;
    z-index: 1;
    border-top: 25px solid #111111;
    border-right: 25px solid transparent;
}
.section1 .content .inner{
    position: relative;
    padding: 30px 45px;
    text-align: center;
    border-radius: 20px 0px 0px 0px;
    background-color: rgba(139,161,34,0.95);
}
.section1 .content .num{
    font-family: "Nunito", sans-serif;
    color: #ffffff;
    font-size: 60px;
    font-weight: 700;
    line-height: 1em;
}
.section1 .content h5{
    font-family: "Nunito", sans-serif;
    position: relative;
    color: #ffffff;
    font-weight: 700;
    margin-top: 10px;
    text-transform: uppercase;
}
/***********============================================== section1 =====================================================***********/

/***********============================================== section2 =====================================================***********/
@media only screen and (max-width: 600px) {.section2{padding:50px 20px 80px 20px; text-align:left;}}
@media only screen and (min-width: 600px) {.section2{padding:50px 20px 80px 20px; text-align:left;}}
@media only screen and (min-width: 768px) {.section2{padding:50px 20px 80px 20px; text-align:left;}} 
