.global_presence_page .dropdown a {
    text-decoration: none;
    width: 100% !important;
}
.global_presence_page .dropdown [data-toggle="dropdown"] {
    position: relative;
    display: block;
    color: white;
    background: #8ba122;
    -moz-box-shadow: 0 1px 0 #8ba122 inset, 0 -1px 0 #8ba122 inset;
    -webkit-box-shadow: 0 1px 0 #8ba122 inset, 0 -1px 0 #8ba122 inset;
    box-shadow: 0 1px 0 #8ba122 inset, 0 -1px 0 #8ba122 inset;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.3);
    padding: 10px;
    width: 100% !important;
}
.global_presence_page .dropdown [data-toggle="dropdown"]:hover {
    background: #8ba122;
}
.global_presence_page .dropdown .icon-arrow {
    position: absolute;
    display: block;
    font-size: 0.7em;
    color: #fff;
    top: 14px;
    right: 10px;
}
.global_presence_page .dropdown .icon-arrow.open {
    -moz-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    transform: rotate(-180deg);
    -moz-transition: -moz-transform 0.6s;
    -o-transition: -o-transform 0.6s;
    -webkit-transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
}
.global_presence_page  .dropdown .icon-arrow.close {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -moz-transition: -moz-transform 0.6s;
    -o-transition: -o-transform 0.6s;
    -webkit-transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
}
.global_presence_page .dropdown .icon-arrow:before {
    content: '\25BC';
}
.global_presence_page .dropdown .dropdown-menu {
    max-height: 0;
    overflow: hidden;
    list-style: none;
    padding: 0;
    margin: 0;
}
.global_presence_page .dropdown .dropdown-menu li {
    padding: 0;
}
.global_presence_page .dropdown .dropdown-menu li a {
    display: block !important;
    color: #6f6f6f;
    background: #EEE;
    -moz-box-shadow: 0 1px 0 white inset, 0 -1px 0 #d5d5d5 inset;
    -webkit-box-shadow: 0 1px 0 white inset, 0 -1px 0 #d5d5d5 inset;
    box-shadow: 0 1px 0 white inset, 0 -1px 0 #d5d5d5 inset;
    text-shadow: 0 -1px 0 rgba(255, 255, 255, 0.3);
    padding: 10px 10px;
    width: 100% !important;
}
.global_presence_page .dropdown .dropdown-menu li a:hover {
    background: #f6f6f6;
    width: 100% !important;
